import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  isOpen: '',
};

export const modalSlice = createSlice({
  name: 'modal',
  initialState,
  
  reducers: {
    isOpen: (state, action) => {
      state.isOpen = action.payload;
    },
  },
});

export const { isOpen } = modalSlice.actions;
